.application-content{
      background: white;
      width: 100%;
      margin-top: 40px;
      padding: 40px;
}
.title-name{
font-family: Inter;
font-size: 24px;
font-weight: 600;
line-height: 28px;
letter-spacing: 0em;
text-align: left;
color: black;
display: inline-block;
margin-right: 20px;

}
.title-badge-count{
font-family: Inter;
font-size: 20px;
font-weight: 600;
line-height: 28px;
letter-spacing: 0em;
text-align: left;
color: #BABABA;
}
.table-head{
background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0))),
-webkit-gradient(linear, left bottom, left top, from(rgba(51, 128, 255, 0.15)), to(rgba(51, 128, 255, 0.15)));
background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0)),
-webkit-linear-gradient(bottom, rgba(51, 128, 255, 0.15), rgba(51, 128, 255, 0.15));
background: linear-gradient(0deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0)),
linear-gradient(0deg, rgba(51, 128, 255, 0.15), rgba(51, 128, 255, 0.15));

}
.device-name-area{
    width: 100%;
    height: 38px;
}
.device-name{
    width: 100%;
    height: 38px;
    border: 1px solid rgb(197, 196, 196);
    border-radius: 5px;
    display: inline-block;
    padding-left: 5px;
    cursor: pointer;
}
.device-add{
    width: 8%;
    height: 38px;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    background-color: rgb(106, 106, 238);
    padding: 3px;
    text-align: center;
    float: right;
}
.device-span{
    margin-top: 6px;
    margin-left: 2px;
    width: auto;
}
.device-cancel{
    background-color: rgb(228, 235, 240);
    height: 30px;
    cursor: pointer;
}
